import React, { useEffect, useState } from 'react';
import { Redirect } from 'react-router-dom';
import '../style/studentaid_lost.css';

const Studentaid_found = () => {

    const [linkSF, setLinkSF] = useState(false);

    useEffect(() => {
        document.body.className='SAL-body';
    });
    
    const individualFound = (e) => {
        e.preventDefault();
        setLinkSF(true);
    };

    if (linkSF) {
        return <Redirect to = '/search_found' />
    };

    return (
        <div>
            {/* header */}
            <div className="SAL-header">
                <h2> </h2>
            </div>
            {/*  / search_container  */}
            <div className="SAL-search_container">
                <input 
                    type="search" 
                    value="" 
                    className="SAL-search_text" 
                    placeholder=" " 
                />
                <button type="submit" className="SAL-search_btn"></button>      
            </div>
            {/*   search_option ( or ) */}
            <div className="SAL-search_option">
                <button type="button" className="SAL-Indi_btn" onClick={e => individualFound(e)}></button>
                <button type="button" className="SAL-Offi_btn SAL-circle_btn"></button>
            </div>
            {/*    offi_list */}
            <div className="SAL-offi-list">
                {/*  -   offi-item */}
                <div className="SAL-offi-item">
                    <div className="SAL-offiItem-info">
                        <h3>  </h3>
                        <p>[]  1 </p>
                    </div>
                    <div className="SAL-offi-date">
                        2021.07.20
                    </div>
                </div>
                {/*  -   offi-item */}
                <div className="SAL-offi-item">
                    <div className="SAL-offiItem-info">
                        <h3> </h3>
                        <p>[]  1 </p>
                    </div>
                    <div className="SAL-offi-date">
                        2021.07.19
                    </div>
                </div>
                {/*  -   offi-item */}
                <div className="SAL-offi-item">
                    <div className="SAL-offiItem-info">                
                        <div>
                            {/*  offiItem-fin */}
                            <div className="SAL-offiItem-fin">[]</div>
                            <h3>E  </h3>
                        </div>
                        <p>[]  </p>
                    </div>
                    <div className="SAL-offi-date">
                        2021.07.17
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Studentaid_found;