import React, { useState, useEffect } from 'react';
import { Redirect, useHistory } from 'react-router-dom';
import { connect } from 'react-redux';
import { read } from '../actions/post';
import { update } from '../actions/apost';
import '../style/new_lost.css';
import CreateMap from '../components/CreateMap';
import M_buildings from "../buildings/M_buildings"
import Y_buildings from "../buildings/Y_buildings"
import cssImage from '../img/skkulogo.png';

const Edit_lost = ({ read, update, isAuthenticated, list }) => {
    
    useEffect(() => {
        if (isAuthenticated) {
            document.body.className='NL-body';
        }

        // if (list.building === ' ( )') {
        //     setMapButton(true);
        //     if (list.address !== ''){
        //         setIsAddress(true);
        //     }
        // }
    });

    let history = useHistory();

    // submit  read  page  
    const [skip, setSkip] = useState(false);
    // map 
    const [map, setMap] = useState(false);
    const [mapButton, setMapButton] = useState(false);
    const [isAddress, setIsAddress] = useState(false);
    //      
    const [postUpdated, setPostUpdated] = useState(false);
    // page   page 
    const [page, setPage] = useState(true);
    //    
    const [imageData, setImageData] = useState({ image: null });
    const [postData, setPostData] = useState({
        id: list.id,
        user: list.user,
        lost_found: list.lost_found,
        title: list.title,
        campus: list.campus,
        building: list.building,
        latitude: list.latitude,
        longitude: list.longitude,
        address: list.address,
        location: list.location,
        date: list.date,
        item: list.item,
        color: list.color,
        content: list.content
    });

    const { id, user, lost_found, title, campus, building, latitude, longitude, address, location, date, item, color, content } = postData;
    const { image } = imageData;

    //     
    const [buildingList, setBuildingList] = useState([<option value=''></option>]);

    // ,     
    const seoul = M_buildings.datas.map((building) => building.name);
    const suwon = Y_buildings.datas.map((building) => building.name);


    const onChangeImage = (e) => {
        setImageData({ ...imageData, [e.target.name]: e.target.files[0] });
    };

    //      !!
    const onChangeBuilding = (e) => {
        //        
        setIsAddress(false);
        if (e.target.value !== '' && e.target.value !== ' ( )') {
            //  ( )      
            setMapButton(false);
            //         
            if (campus === 'M') {
                M_buildings.datas.map((data) => {
                    if (e.target.value === data.name) {
                        setPostData({
                            ...postData,
                            [e.target.name]: e.target.value,
                            ['latitude']: data.latitude,
                            ['longitude']: data.longitude,
                            ['address']: data.address
                        });
                    }
                });
            //         
            } else if (campus === 'Y') {
                Y_buildings.datas.map((data) => {
                    if (e.target.value === data.name) {
                        setPostData({
                            ...postData,
                            [e.target.name]: e.target.value,
                            ['latitude']: data.latitude,
                            ['longitude']: data.longitude,
                            ['address']: data.address
                        });
                    }
                });
            }
        } 
        //          
        else {
            //     
            if (e.target.value === ' ( )') {
                setMapButton(true);
            }
            //        
            else if (e.target.value === '') {
                setMapButton(false);
            }
            setPostData({
                ...postData,
                [e.target.name]: e.target.value,
                ['latitude']: '',
                ['longitude']: '',
                ['address']: ''
            });
        }
    };

    const onChange = (e) => {
        if (e.target.name === "campus") {
            let elemArray=[];
            if (e.target.value === "M") {
                for (let i = 0; i < seoul.length; i++) {
                    elemArray.push(<option value={seoul[i]}>{seoul[i]}</option>);
                }
                //    ,    
                setMapButton(false);
                setPostData({
                    ...postData,
                    [e.target.name]: e.target.value,
                    ['building']: '',
                    ['latitude']: '',
                    ['longitude']: '',
                    ['address']: ''
                });
                
            } else if (e.target.value === "Y") {
                for (let i = 0; i < suwon.length; i++) {
                    elemArray.push(<option value={suwon[i]}>{suwon[i]}</option>);
                }
                //    ,    
                setMapButton(false);
                setPostData({
                    ...postData,
                    [e.target.name]: e.target.value,
                    ['building']: '',
                    ['latitude']: '',
                    ['longitude']: '',
                    ['address']: ''
                });

            } else {
                //         
                elemArray.push(<option value=''></option>);
                //    ,    
                setMapButton(false);
                setPostData({
                    ...postData,
                    [e.target.name]: e.target.value,
                    ['building']: '',
                    ['latitude']: '',
                    ['longitude']: '',
                    ['address']: ''
                });
            }
            setBuildingList(elemArray);
            //      
        } else {
            setPostData({...postData, [e.target.name]: e.target.value});
        }
    };


    const onSubmit = e => {
        e.preventDefault();

        const formData = new FormData();
        formData.append('user', user);
        formData.append('lost_found', lost_found);
        formData.append('title', title);
        formData.append('campus', campus);
        formData.append('building', building);
        formData.append('latitude', latitude);
        formData.append('longitude', longitude);
        formData.append('address', address);
        formData.append('location', location);
        formData.append('date', date);
        formData.append('item', item);
        formData.append('color', color);
        formData.append('content', content);
        //  null   
        if (image!==null){
            formData.append('image', image);
        }
        formData.append('content', content);

        update(id, formData);
    };

    //  submit  update  list     
    useEffect(() => {
        //   
        if (!skip) {
            setSkip(true);
        } else {
            read();
            setPostUpdated(true);
        }
    },[list]);

    //     
    const getAddress = (latitude, longitude, address) => {

        //       
        if (address !== '') {
            setIsAddress(true);
        } else {
            setIsAddress(false);
        }

        setPostData({
            ...postData,
            ['latitude']: latitude,
            ['longitude']: longitude,
            ['address']: address
        });
    };

    //      
    if (!isAuthenticated) {
        return <Redirect to = '/login' />
    }

    //     
    if (postUpdated) {
        return <Redirect to = '/detail_lost'/>
    }

    //  
    const openModal = (e) => {
        e.preventDefault();
        setMap(true);
    }

    //  
    const closeModal = () => {
        setMap(false);
    }

    //  
    const mapView = () => (
        <div className="modal">
            <div className="modal__overlay" onClick={closeModal}></div>
            <div className="modal__content">
                <CreateMap mapCreator={getAddress} mapOff={closeModal} campus={campus} />
            </div>
        </div>
    );

    //  
    const noView = () => (
        <div></div>
    );

    //   
    const closeIsAddress = () => {

        setIsAddress(false);
        //     
        setPostData({
            ...postData,
            ['latitude']: '',
            ['longitude']: '',
            ['address']: ''
        });
    }

    //        
    const showAddress = () => (
        <div className="NL-campus_detail">
            <div className="NL-new_theme_detail">{address}</div>
            <div className="NL-new_sel_detail">
                <button className="NL-campus_opt_detail" onClick={closeIsAddress}> 
                        x
                </button>
            </div>
        </div>

    );

    //   
    const mapButtonPop = () => (
        <div>
            <div className="NL-campus">
                <div className="NL-new_theme"> </div>
                <div className="NL-new_sel">
                    <button className="NL-campus_opt" onClick={openModal}> 
                        
                    </button>
                    {map ? mapView() : noView()}
                </div>
            </div>
            {isAddress ? showAddress() : noView()}
        </div>
    );

    //   
    const noButtonPop = () => (
        <div></div>
    );

    //    (pg1 -> pg2)
    const nextPage = (e) => {
        e.preventDefault();
        setPage(false);
    };

    //   
    const goBack = () => {
        if (page) {
            history.goBack();
        } else {
            setPage(true);
        }
    };

    const page1 = () => (
        <div>
            <div className="NL-new_exp" id='pg1'>
                <h2> </h2>
                <p>   , <br />     !</p>
            </div>
            <div className="NL-new_category">
                <div className="NL-campus">
                    <div className="NL-new_theme"> </div>
                    <div className="NL-new_sel">
                        <select 
                            className="NL-campus_opt" 
                            name='campus'
                            value={campus}
                            onChange={e => onChange(e)}
                            required
                        >
                            <option value=""></option>
                            <option value="M"> </option>
                            <option value="Y"> </option>
                        </select>
                    </div>
                </div>
                <div className="NL-campus">
                    <div className="NL-new_theme"> </div>
                    <div className="NL-new_sel">
                        <select 
                            className="NL-campus_opt" 
                            name='building' 
                            value={building}
                            onChange={e => onChangeBuilding(e)} 
                            required
                        >{buildingList}</select>
                    </div>
                </div>
                {/*      */}
                {mapButton ? mapButtonPop() : noButtonPop()}
                <div className="NL-campus">
                    <div className="NL-new_theme"> </div>
                    <div className="NL-new_sel">
                        <select 
                            className="NL-campus_opt" 
                            name='item' 
                            value={item} 
                            onChange={e => onChange(e)} 
                            required
                        >
                            <option value=''></option>
                            <option value=""></option>
                            <option value="/">/</option>
                            <option value="/">/</option>
                            <option value=""></option>
                        </select>
                    </div>
                </div>
                <div className="NL-campus">
                    <div className="NL-new_theme"> </div>
                    <div className="NL-new_sel">
                        <select 
                            className="NL-campus_opt" 
                            name='color' 
                            value={color} 
                            onChange={e => onChange(e)} 
                            required
                        >
                            <option value=''></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                        </select>
                    </div>
                </div>
                <div className="NL-campus">
                    <div className="NL-new_theme"> </div>
                    <div className="NL-new_sel">
                        <input
                            type='date'
                            name='date'
                            value={date}
                            onChange={e => onChange(e)}
                            required
                        />
                    </div>
                </div>
            </div>
            <button className="NL-new_next" onClick={e => nextPage(e)}></button>
        </div>
    );

    const page2 = () => (
        <div>
            <div className="NL-new_exp" id='pg2'>
                <h2> </h2>
                <p>       <br/>     !</p>
            </div>
            <div className="NL-new_insert_img">
                <img src={cssImage} />
            </div>
            <div className="NL-new_img_button">
            <label className="NL-input-file-button" htmlFor="input-file">
                    { image === null ? ' ' : ' ' }
                </label>
                <input
                    type='file'
                    accept='image/*'
                    name='image'
                    id='input-file'
                    onChange={e => onChangeImage(e)}
                    style={{display:'none'}}
                />
            </div>
            <div className="NL-new_title_input">
                <input 
                    type="text" 
                    placeholder=" " 
                    name='title' 
                    value={title} 
                    onChange={e => onChange(e)} 
                    required 
                />
            </div>
            <div className="NL-new_place_input">
                <input 
                    type="text" 
                    placeholder=" " 
                    name='location' 
                    value={location} 
                    onChange={e => onChange(e)} 
                    required 
                />
            </div>
            <div className="NL-new_content_input">
                <p><textarea
                    name='content'
                    value={content}
                    onChange={e => onChange(e)}
                    placeholder="
                       
                    &#13;&#10;
                    <  >
                          /         ,       .

                           ,     ,       .

                         1 1  , /           ." 
                    required
                    /></p>
            </div>
            <button className="NL-new_complete" type='submit'>  </button>
        </div>
    );

    return(
        <div>
            <header className="NL-header"> 
                <div className="NL-back">
                    <button onClick={goBack}><i className="fa fa-chevron-left"></i></button>
                </div>
                <h2> </h2>
            </header>
            <form onSubmit={e => onSubmit(e)}>
                {/*     page   page  */}
                {page ? page1() : page2 ()}
            </form>
        </div>
    );
};

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated,
    list: state.apost._data
});

export default connect(mapStateToProps, { read, update })(Edit_lost);